import { singleton } from "../fable-library.3.1.7/AsyncBuilder.js";
import { fold } from "../fable-library.3.1.7/Map.js";
import { some } from "../fable-library.3.1.7/Option.js";

export function Async_map(mapper, a) {
    return singleton.Delay(() => singleton.Bind(a, (_arg1) => {
        const res = _arg1;
        return singleton.Return(mapper(res));
    }));
}

export function Async_lift(value) {
    return singleton.Delay(() => singleton.Return(value));
}

export function Map_toJS(map) {
    return fold((jsMap, k, v) => jsMap.set(k, some(v)), new Map(), map);
}

export function Promise_ignore(prom) {
    return prom.then(((value) => {
    }));
}

