export const gun = gun_1;

export function createUser(user, username, password) {
    return new Promise(((res, err) => {
        try {
            user.create(username, password, res);
        }
        catch (ex) {
            toConsole(interpolate("createUser error: %P()", [ex]));
            err(ex);
        }
    }));
}

export function authUser(user, username, password) {
    return new Promise(((res, err) => {
        try {
            user.auth(username, password, res);
        }
        catch (ex) {
            toConsole(printf("authUser error: {ex}"));
            err(ex);
        }
    }));
}

